.loading {
	min-height: 600px;
	width: 100%;
	display: inline-block;
	position: relative;
	background-color: white;
	border-radius: 5px;
}

.loading.out {
	-webkit-animation: fadeout 1s;
	        animation: fadeout 1s;
}
.spinner {
	position: absolute;
	left: 50%;
	top: 50%;
	margin: -60px 0 0 -60px;
	background: #fff;
	width: 100px;
	height: 100px;
	border-radius: 100%;
	border: 10px solid #19bee1;
}
.spinner:after {
	content: "";
	background: transparent;
	width: 140%;
	height: 140%;
	position: absolute;
	border-radius: 100%;
	top: -20%;
	left: -20%;
	opacity: 0.7;
	-webkit-box-shadow: rgba(13, 138, 164, 0.6) -4px -5px 3px -3px;
	        box-shadow: rgba(13, 138, 164, 0.6) -4px -5px 3px -3px;
	-webkit-animation: rotate 2s infinite linear;
	        animation: rotate 2s infinite linear;
}

@-webkit-keyframes rotate {
	0% {
		-webkit-transform: rotateZ(0deg);
		        transform: rotateZ(0deg);
	}
	100% {
		-webkit-transform: rotateZ(360deg);
		        transform: rotateZ(360deg);
	}
}

@keyframes rotate {
	0% {
		-webkit-transform: rotateZ(0deg);
		        transform: rotateZ(0deg);
	}
	100% {
		-webkit-transform: rotateZ(360deg);
		        transform: rotateZ(360deg);
	}
}

@-webkit-keyframes fadeout {
	from {
		opacity: 1;
	}
	to {
		opacity: 0;
	}
}

@keyframes fadeout {
	from {
		opacity: 1;
	}
	to {
		opacity: 0;
	}
}



